export const useGetReserves = (lpAddress: string) => {
  const { slowRefresh } = useRefresh()
  const [reserves, setReserves] = useState(new BigNumber(0))

  useEffect(() => {
    async function fetchGetReserves() {
      const cakeContract = getContract(pairABI, lpAddress)
      const rbs = await cakeContract.methods.getReserves().call()
      setReserves(new BigNumber(rbs?._reserve1).div(new BigNumber(10 ** 18)))
    }

    fetchGetReserves()
  }, [slowRefresh, lpAddress])

  return reserves
}

